.content_q {
  color: #000; }
  .content_q .title {
    font-size: 48px;
    display: flex;
    flex-direction: row;
    text-align: left; }
    .content_q .title p {
      display: inline; }
    .content_q .title span {
      height: 70px;
      border: 4px solid #9F1213;
      margin-right: 16px; }
      @media (max-width: 375px) {
        .content_q .title span {
          height: 40px;
          border: 2px solid #9F1213;
          margin-right: 8px; } }
    @media (max-width: 375px) {
      .content_q .title {
        font-size: 28px; } }

.main_que .subtitle {
  font-size: 32px;
  font-weight: bold; }
  @media (max-width: 375px) {
    .main_que .subtitle {
      font-size: 20px; } }

.main_que .question .que {
  font-size: 20px;
  margin-bottom: 16px;
  font-weight: bold; }
  @media (max-width: 375px) {
    .main_que .question .que {
      font-size: 16px;
      margin-bottom: 8px; } }

.main_que .question .ans {
  font-size: 16px;
  padding-left: 95px; }
  .main_que .question .ans .mobile {
    display: none; }
  @media (max-width: 375px) {
    .main_que .question .ans {
      font-size: 14px;
      padding-left: 0; }
      .main_que .question .ans .non-mobile {
        display: none; }
      .main_que .question .ans .mobile {
        display: block; } }
